import {PolyScene} from '../../@polygonjs/polygonjs/src/engine/scene/PolyScene';

import {SceneJsonExporterData} from '../../@polygonjs/polygonjs/src/engine/io/json/export/Scene';
import {EditorLayoutData} from '../editor/types';

export interface LoadData {
	scene_code: SceneJsonExporterData;
	editor_data: EditorLayoutData;
	sceneName: string | null;
}

type OnSceneLoadedCallback = (scene: PolyScene) => Promise<void>;
export class AppTestHelperClass {
	private static _instance: AppTestHelperClass;
	static instance() {
		return (this._instance = this._instance || new AppTestHelperClass());
	}
	private constructor() {}

	private _onSceneLoadedCallback: OnSceneLoadedCallback | undefined;

	setOnSceneLoadedCallback(callback: OnSceneLoadedCallback) {
		this._onSceneLoadedCallback = callback;
	}
	async onSceneLoaded(scene: PolyScene) {
		if (this._onSceneLoadedCallback) {
			await this._onSceneLoadedCallback(scene);
		}
	}
}

export const AppTestHelper = AppTestHelperClass.instance();
